<template>
  <div class="container">

    <h1 class="text-center"  v-text="post.title"></h1>

    <hr>

    <div class="row justify-content-around shadow p-3 mb-2 bg-white rounded">
        <div class="col-3">
            <h4 class="text-center">Autor</h4> 
            <p class="text-center">{{user.name}}| {{user.username}}</p>
            <hr>
            <p v-text="user.email" ></p>
            <p v-text="user.phone" ></p>
            <p v-text="user.website" ></p>
            <p v-text="user.address.city" ></p>
            <hr>
            <p v-text="user.company.name" ></p>
            <br>
            <button class="btn btn-secondary" @click="back">Back</button>
        </div>
        <div class="col-6">
            <p v-text="post.body"></p>
        </div>
    </div>

  </div>
</template>

<script>
export default {
    name: 'SinglePostComponent',
    data(){
        return {
            id: this.$route.params.id,
            post:{},
            user:{}
        }
    },
    methods:{
        getPost: async function(){
            const data = await fetch(`https://jsonplaceholder.typicode.com/posts/${this.id}`)
            const _post = await data.json()
            this.post = _post

            this.getOwner(_post.userId)
        },
        getOwner: async function(id){
            const data = await fetch(`https://jsonplaceholder.typicode.com/users/${id}`)
            const _owner = await data.json()
            this.user = _owner
        },
        back(){
            this.$router.go(-1)
        }
    },
    mounted(){
        this.getPost()
    }
}
</script>

