<template>
    <div>
        <div v-for="(item, index) of posts" :key="index"
            class="list-group shadow p-3 mb-2 bg-white rounded">
            <div class="d-flex w-100 justify-content-between">
                <h5 class="mb-1" v-text="item.title"></h5>

                <small>
                    <router-link :to="{name:'post', params:{id:item.id}}">
                        Ver mÃ¡s
                    </router-link>
                </small>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name:'PostComponent',
    data(){
        return {
            posts:[]
        }
    },
    methods:{
        getPosts: async function(){
            const data = await fetch('https://jsonplaceholder.typicode.com/posts')
            const _post = await data.json()
            this.posts = _post
        }
    },
    created(){

    },
    mounted(){
        this.getPosts()
    }
    
}
</script>

